// Wrapper
@mixin wrapper($width) {
  max-width: $width;
  margin: 0 auto;
}

// List
@mixin list-reset {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
}

// Link
@mixin link-reset {
  text-decoration: none;
  font-family: inherit;
  color: inherit;
}

// Button
@mixin button-reset {
  background-color: transparent;
  border: none;
  padding: 0;
  font-family: inherit;
}

// Media widths
@mixin media($width) {
  @media (max-width: $width) {
    @content;
  }
}