:root {
  --black: #0000009a;
  --white: #ffffff;
  --silver: #c8c8c89a;
  --dove-gray: #6e6e6e;
  --punch: #d92c2c;
  --seashell: #e7e2e2de;
  --tundora: #444444bb;
  --laurel: #007E06;
  --large: 26px;
  --medium: 22px;
  --small: 18px;
  --desktop-width: 1280px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

body {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.content-container {
  max-width: var(--desktop-width);
  margin: 0 auto;
}

.section-container {
  display: grid;
  gap: 2rem 0;
}

.popup {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  display: none;
}
.popup--active {
  background-color: var(--laurel);
}
.popup--disabled {
  background-color: var(--punch);
}
.popup--visible {
  display: block;
}
.popup__container {
  padding: 0.5rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.popup__message {
  margin: 0;
  font-size: var(--medium);
  color: var(--white);
}
.popup__button {
  cursor: pointer;
  width: 25px;
  height: 25px;
}

.title {
  margin: 0;
  font-size: var(--large);
  justify-self: center;
}

.main {
  display: grid;
  gap: 4rem 0;
  padding: 4rem 0 2rem;
  background: var(--seashell);
  font-family: "Lora", serif;
  position: relative;
}

.maps__list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.maps__image {
  transition: scale 0.3s ease-in-out;
}
.maps__image-description {
  font-size: var(--large);
  font-weight: 700;
  color: var(--white);
  opacity: 0;
  background-color: var(--black);
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.15s ease-out;
}
.maps__image-container {
  margin: 0;
  overflow: hidden;
  position: relative;
}
.maps__image-container:hover > img {
  scale: 1.1;
}
.maps__image-container:hover > figcaption {
  opacity: 1;
}

.rules__list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  padding: 1rem;
  display: grid;
  justify-content: center;
  align-items: flex-start;
  gap: 10px 0;
  max-height: 900px;
}
.rules__item {
  border: 1px solid var(--silver);
  border-radius: 8px;
  width: 600px;
  box-shadow: 1px 2px 1px 0 var(--silver);
}
.rules__title {
  position: relative;
  display: block;
  padding: 1rem;
  font-size: var(--medium);
  cursor: pointer;
}
.rules__title::after {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  right: 10%;
  transform: translateY(-50%);
  background-image: url(../images/rules/arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  transition: transform 0.25s ease-in-out;
  transform-origin: center;
}
.rules__input:checked ~ .rules__content {
  display: block;
}
.rules__input:checked ~ .rules__title::after {
  top: 35%;
  transform: rotate(180deg);
}
.rules__content {
  display: none;
  margin: 0;
  padding: 0.5rem 1rem;
  font-size: var(--small);
  line-height: 1.3;
  border-top: 1px dashed var(--tundora);
}