.popup {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  display: none;

  &--active {
    background-color: var(--laurel);
  }

  &--disabled {
    background-color: var(--punch);
  }

  &--visible {
    display: block;
  }

  &__container {
    padding: 0.5rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__message {
    margin: 0;
    font-size: var(--medium);
    color: var(--white);
  }

  &__button {
    cursor: pointer;
    width: 25px;
    height: 25px;
  }
}