.maps {
  &__list {
    @include list-reset;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }

  &__image {
    transition: scale 0.3s ease-in-out;
  }

  &__image-description {
    font-size: var(--large);
    font-weight: 700;
    color: var(--white);
    opacity: 0;
    background-color: var(--black);

    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.15s ease-out;
  }

  &__image-container {
    margin: 0;
    overflow: hidden;
    position: relative;

    &:hover > img {
      scale: 1.1;
    }

    &:hover > figcaption {
      opacity: 1;
    }
  }
}