.rules {
  &__list {
    @include list-reset;
    padding: 1rem;
    display: grid;
    justify-content: center;
    align-items: flex-start;
    gap: 10px 0;
    max-height: 900px;
  }

  &__item {
    border: 1px solid var(--silver);
    border-radius: 8px;
    width: 600px;
    box-shadow: 1px 2px 1px 0 var(--silver);
  }

  &__title {
    position: relative;
    display: block;
    padding: 1rem;
    font-size: var(--medium);
    cursor: pointer;

    &::after {
      content: "";
      display: block;
      width: 20px;
      height: 20px;
      position: absolute;
      top: 50%;
      right: 10%;
      transform: translateY(-50%);
      background-image: url(../images/rules/arrow.svg);
      background-repeat: no-repeat;
      background-position: center;
      transition: transform 0.25s ease-in-out;
      transform-origin: center;
    }
  }

  &__input:checked ~ &__content {
    display: block;
  }

  &__input:checked ~ &__title::after {
    top: 35%;
    transform: rotate(180deg);
  }

  &__content {
    display: none;
    margin: 0;
    padding: 0.5rem 1rem;
    font-size: var(--small);
    line-height: 1.3;
    border-top: 1px dashed var(--tundora);
  }
}
